.page-template-modular-page {

    section {
        margin-bottom: 85px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 130px;
        }
    }

    .hero {
        margin-bottom: 80px;
    }

    .top-banner {
        &#mod-1 {
            margin-bottom: 0;
        }

        &:not(#mod-1) {
            h1 {
                margin-bottom: 25px;
            }
        }

        & + .con-img {
            @include media-breakpoint-down(md) {
                padding-top: 20px;
            }
        }
    }

    .con-img {
        @include media-breakpoint-up(lg) {
            padding-top: 20px;
        }

        &.is-reversed {
            @include media-breakpoint-down(md) {
                margin-bottom: 0;
            }
        }
    }

    .highlights-slider {
        @include media-breakpoint-up(lg) {
            margin-bottom: 40px;
        }
    }

    .image-grid-small {
        margin-bottom: 100px;
    }
}